import React from "react"
import { graphql, Link } from "gatsby"

export default function SingleProductPage({ data: { product } }) {
  return (
    <div style={{ overflow: "hidden" }}>
      <Link to="/">
        <p>Home</p>
      </Link>
      <h1 style={{ position: "relative", display: "inline-block" }}>
        {product.name}
        {product.on_sale && (
          <span
            style={{
              fontSize: 14,
              borderRadius: "100%",
              background: "rebeccapurple",
              color: "#fff",
              padding: 10,
              position: "absolute",
              right: -50,
              top: -20,
              transform: "rotate(20deg)",
            }}
          >
            SALE!
          </span>
        )}
      </h1>
      <div>{product.stock_quantity} in stock (info icon)</div>
      <div dangerouslySetInnerHTML={{ __html: product.price_html }} />
      {product.images &&
        product.images.map((image, i) => (
          <img
            key={i}
            src={image.src}
            alt={image.name}
            style={{ display: "block", width: "100%", marginBottom: "1rem" }}
          />
        ))}
    </div>
  )
}

// This needs to be dynamic based on the slug passed in via context in gatsby-node.js
export const query = graphql`
  query($id: String!) {
    product: wcProduct(id: { eq: $id }) {
      name
      id
      wooCommerceId
      slug
      on_sale
      stock_quantity
      images {
        name
        src
      }
      price_html
    }
  }
`
